import { Component, OnInit } from '@angular/core';
import { NavController,  AlertController, ActionSheetController, LoadingController } from '@ionic/angular';
import { AppMin } from '../core';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';




@Component({
  selector: 'app-my-explain-txt-set-expert',
  templateUrl: './my-explain-txt-set-expert.page.html',
  styleUrls: ['./my-explain-txt-set-expert.page.scss'],
})
export class MyExplainTxtSetExpertPage implements OnInit {
  header_class;
  serviceTxt = "";
  loader;
  constructor(public appmin: AppMin, public navCtrl: NavController,  public alertCtrl: AlertController, public actionSheetCtrl: ActionSheetController, public loadingCtrl?: LoadingController, private camera?: Camera) { 

    //------------------动态设置导航栏高度start
    if (appmin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      console.log("判断为安卓系统 设置状态栏样式")
    }
    //------------------动态设置导航栏高度end
    this.serviceTxt = this.serviceTxt.trim();

    

  }
  userTxt_upkey(value) {
    //简介输入框 输入事件
    this.serviceTxt = value;
  }
  baoc_click() {
    //保存按钮

    let obj = {
      type: "setExpert",
      userId: this.appmin.getUserid(),
      expert: this.serviceTxt

    }
    // this.send(JSON.stringify(obj), true);
   // this.appmin.send(JSON.stringify(obj))

    this.appmin.post(obj).subscribe((data) => {
      this.dataHandle(data);
    })
    

  }
  ionViewWillEnter(){
   
    this.addExpert();
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad MyExplainTxtSetExpertPage');
  }
  addExpert() {
    //删除内容
    let obj = {
      type: "addExpert",
      userId: this.appmin.getUserid(),
    }
   // this.appmin.send(JSON.stringify(obj));

    this.appmin.post(obj).subscribe((data) => {
      this.dataHandle(data);
    })
    
  }

  dataHandle(msg) {
 
    if (msg.type == 'addExpert_ret') {
      console.log("addExpert_ret="+JSON.stringify(msg));
      //收到加载数据
      this.serviceTxt = msg.expert;
    }
    if (msg.type == 'setExpert_ret') {
      //设置擅长完成 返回
      this.navCtrl.pop();
    }
  }

  ngOnInit() {
  }

}
